import interfaceurl from '@/utils/interface.js'
export default {
	data() {
		return {
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			bannerList: [],
			id: 1,
			//动态参数
			topCagetogyList: [], //获取一级分类
			categoryList: [] //获取二级和三级分类
		}
	},
	onLoad(options) {
		
	},
	onShow() {
		this.getTopCagetogyList()
	},
	methods: {
		//获取一级分类
		getTopCagetogyList() {
			let that = this;
			interfaceurl.checkAuth(interfaceurl.firstList, {}, false).then((res) => {
				that.topCagetogyList = res.data
				that.id = that.topCagetogyList[0].cateId
				that.getCategoryList()
			});
		},
		//获取子分类
		getCategoryList() {
			let that = this;
			let params = {
				cateId: that.id,
				page: 2,
				size: 10
			}
			interfaceurl.checkAuth(interfaceurl.childList, params, false).then((res) => {
				that.categoryList = res.data
			});
		},
		selectTab(item) {
			this.id = item.cateId
			this.getCategoryList();
		}
	}
}
